<style lang="scss">.flex-with-space {
  flex: 1 1 auto;
  justify-content: flex-end;
  font-size: 1rem; }

.pub-image {
  width: 7rem; }

.publication-card {
  margin: 0.5rem 0.5rem 2rem 0.5rem; }

.with-space {
  margin-left: 0.5rem;
  position: relative; }

.shifted-icon {
  position: absolute;
  font-size: 1rem;
  margin: 0.25rem 0 0 0.25rem; }
</style>

<script lang="ts">import Paper, { Title, Content } from '@smui/paper';
;
import { onMount, onDestroy } from 'svelte';
import { getRelativeDate } from '../helpers/date';
export let publication = {
    service: '',
    url: '',
    lang: '',
    fullUrl: '',
    meta: {
        author: null,
        date: null,
        description: null,
        image: null,
        logo: null,
        publisher: null,
        title: null,
        url: null
    }
};
let pubDate;
let updater = 0;
const setDate = () => {
    pubDate = getRelativeDate(publication.meta.date);
};
const getImage = (data) => {
    if (data.meta.image) {
        return data.meta.image;
    }
};
onMount(() => {
    setDate();
    updater = window.setInterval(() => setDate(), 1000);
});
onDestroy(() => {
    if (updater) {
        window.clearInterval(updater);
    }
});
</script>

<div class="publication-card">
	<Paper elevation="3">
		<Title>
			<div class="flex-container">
				<div>
					<a href="{publication.fullUrl}" target="_blank" rel="noreferrer noopener">
						<span class="mdc-typography--headline5">{publication.meta.title}</span>
					</a>
				</div>
				<div class="flex-container flex-with-space greyed">
					<div>{pubDate}</div>
				</div>
			</div>
		</Title>
		<Content>
			<div class="flex-container">
				<div><img class="pub-image" src="{getImage(publication)}" alt="publication logo" /></div>
				<div class="with-space">
					<a href="{publication.fullUrl}" target="_blank" rel="noreferrer noopener">
						<span class="mdc-typography--body1">{publication.meta.description}</span>
						<i class="material-icons shifted-icon">launch</i>
					</a>
				</div>
			</div>
		</Content>
	</Paper>
</div>
