<style lang="scss">.flex-right {
  margin: 0 0.5rem;
  justify-content: flex-end; }
</style>

<script lang="ts">;
import Switch from '@smui/switch';
import FormField from '@smui/form-field';
import LinkItem from '../components/LinkItem.svelte';
import { RoutePath } from '.';
import { getSortedList } from '../helpers/sort';
import { getPageTitle } from '../labels';
import { getArticles } from '../helpers/global';
import { Language } from '../data/language';
let selected = false;
const publications = getArticles();
const allPublications = getSortedList(publications);
const enPubs = getSortedList(publications.filter(pub => pub.lang === Language.En));
const getPublications = (isSelected) => isSelected ? [...allPublications] : [...enPubs];
let pubsToRender = getPublications(selected);
const updateList = () => {
    pubsToRender = getPublications(selected);
};
</script>

<svelte:head>
	<title>{getPageTitle(RoutePath.Articles)}</title>
</svelte:head>

<div>
	<div class="flex-container flex-right">
		<div>
			<FormField align="end">
				<Switch bind:checked="{selected}" on:change="{updateList}" />
				<span slot="label">Show publications in Russian</span>
			</FormField>
		</div>
	</div>
	<div>
		{#each pubsToRender as pub (pub.fullUrl)}
			<LinkItem publication="{pub}" />
		{/each}
	</div>
</div>
